@import url(https://fonts.googleapis.com/css2?family=Inconsolata);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
	margin: 0;
	font-family: "Montserrat", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
	height: 100%;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.timer{display:flex;align-items:center;justify-content:center;font-size:xxx-large;height:100px;width:100px;border:5px solid orange;border-radius:50%}.box{font-family:Inconsolata,monospace;font-weight:700;padding:200px 200px;font-size:21pt;display:flex;flex-wrap:wrap;align-items:center}.box .word{position:relative;margin-left:10px}.box .typedWord{position:absolute;top:0;left:0}.box #active,.box .right{color:#789}.box #active{position:relative;width:-webkit-max-content;width:-moz-max-content;width:max-content}.box .wrong{color:#ffa07a !important}.box .caret{transition:left .1s ease;margin-left:-8px;position:absolute;color:orange;animation:blink 1s ease infinite}.box .caret span{color:transparent}@keyframes blink{0%,100%{opacity:1}50%{opacity:0}}
